
html {
  background: #fff;
  font-family: STHeiti,Microsoft YaHei,Helvetica,Arial,sans-serif;
  -webkit-text-size-adjust: none;
  word-break: break-word;
}
a {
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
}
.clearfix::after {
  content: ' ';
  display: block;
  clear: both;
}
.line3 {
  -webkit-line-clamp: 3;
}
@font-face {
  font-family: 'toutiao-iconfont';
  /* project id 363854 */
  src: url("//at.alicdn.com/t/font_gunoldcqvx61dcxr.eot");
  src: url("//at.alicdn.com/t/font_gunoldcqvx61dcxr.eot?#iefix") format("embedded-opentype"), url("//at.alicdn.com/t/font_gunoldcqvx61dcxr.woff") format("woff"), url("//at.alicdn.com/t/font_gunoldcqvx61dcxr.ttf") format("truetype"), url("//at.alicdn.com/t/font_gunoldcqvx61dcxr.svg#iconfont") format("svg");
}
.icon {
  font-family: 'toutiao-iconfont';
  font-style: normal;
}
.icon-refresh::before {
  content: '\e62b';
}
.toutiao-nav {
  background-color: #F4F5F6;
}
.toutiao-nav .nav-wrapper {
    width: 90%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    position: relative;
    height: 36px;
    white-space: nowrap;
    overflow: hidden;
}
[data-dpr="2"] .toutiao-nav .nav-wrapper {
      height: 72px;
}
[data-dpr="3"] .toutiao-nav .nav-wrapper {
      height: 108px;
}
.toutiao-nav .nav-wrapper:after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background-image: -webkit-linear-gradient(top, transparent, transparent 40%, #ddd 0);
      background-size: 100% 1px;
      background-position: bottom;
      background-repeat: no-repeat;
}
.toutiao-nav .nav-wrapper .nav-item-wrapper {
      position: absolute;
      white-space: nowrap;
      overflow: hidden;
      overflow-x: scroll;
      -webkit-overflow-scrolling: touch;
}
.toutiao-nav .nav-wrapper .nav-item-wrapper::-webkit-scrollbar {
        display: none;
}
.toutiao-nav .nav-wrapper .nav-item-wrapper .nav-item {
        display: inline-block;
}
.toutiao-nav .nav-wrapper .nav-item-wrapper .nav-item > a {
          display: inline-block;
          height: 26px;
          margin-top: 5px;
          margin-bottom: 5px;
          margin-left: 5px;
          padding-left: 10px;
          padding-right: 10px;
          font-size: 17px;
          line-height: 26px;
          color: #505050;
          white-space: nowrap;
          -webkit-tap-highlight-color: rgba(0, 0, 0, 0.3);
}
[data-dpr="2"] .toutiao-nav .nav-wrapper .nav-item-wrapper .nav-item > a {
            height: 52px;
}
[data-dpr="3"] .toutiao-nav .nav-wrapper .nav-item-wrapper .nav-item > a {
            height: 78px;
}
[data-dpr="2"] .toutiao-nav .nav-wrapper .nav-item-wrapper .nav-item > a {
            margin-top: 10px;
}
[data-dpr="3"] .toutiao-nav .nav-wrapper .nav-item-wrapper .nav-item > a {
            margin-top: 15px;
}
[data-dpr="2"] .toutiao-nav .nav-wrapper .nav-item-wrapper .nav-item > a {
            margin-bottom: 10px;
}
[data-dpr="3"] .toutiao-nav .nav-wrapper .nav-item-wrapper .nav-item > a {
            margin-bottom: 15px;
}
[data-dpr="2"] .toutiao-nav .nav-wrapper .nav-item-wrapper .nav-item > a {
            margin-left: 10px;
}
[data-dpr="3"] .toutiao-nav .nav-wrapper .nav-item-wrapper .nav-item > a {
            margin-left: 15px;
}
[data-dpr="2"] .toutiao-nav .nav-wrapper .nav-item-wrapper .nav-item > a {
            padding-left: 20px;
}
[data-dpr="3"] .toutiao-nav .nav-wrapper .nav-item-wrapper .nav-item > a {
            padding-left: 30px;
}
[data-dpr="2"] .toutiao-nav .nav-wrapper .nav-item-wrapper .nav-item > a {
            padding-right: 20px;
}
[data-dpr="3"] .toutiao-nav .nav-wrapper .nav-item-wrapper .nav-item > a {
            padding-right: 30px;
}
[data-dpr="2"] .toutiao-nav .nav-wrapper .nav-item-wrapper .nav-item > a {
            font-size: 34px;
}
[data-dpr="3"] .toutiao-nav .nav-wrapper .nav-item-wrapper .nav-item > a {
            font-size: 51px;
}
[data-dpr="2"] .toutiao-nav .nav-wrapper .nav-item-wrapper .nav-item > a {
            line-height: 52px;
}
[data-dpr="3"] .toutiao-nav .nav-wrapper .nav-item-wrapper .nav-item > a {
            line-height: 78px;
}
.toutiao-nav .nav-wrapper .nav-item-wrapper .nav-item > a.router-link-active {
            color: #f85959;
}
.toutiao-nav .more-wrapper {
    position: absolute;
    right: 0;
    background-color: #F4F5F6;
    opacity: 1;
    z-index: 2;
}
.toutiao-nav .more-wrapper .list-shadow {
      position: absolute;
      left: -10px;
      height: 35px;
      width: 10px;
      background: url("./images/shadow.png") no-repeat center;
      background-size: contain;
}
[data-dpr="2"] .toutiao-nav .more-wrapper .list-shadow {
        left: -20px;
}
[data-dpr="3"] .toutiao-nav .more-wrapper .list-shadow {
        left: -30px;
}
[data-dpr="2"] .toutiao-nav .more-wrapper .list-shadow {
        height: 70px;
}
[data-dpr="3"] .toutiao-nav .more-wrapper .list-shadow {
        height: 105px;
}
[data-dpr="2"] .toutiao-nav .more-wrapper .list-shadow {
        width: 20px;
}
[data-dpr="3"] .toutiao-nav .more-wrapper .list-shadow {
        width: 30px;
}
.toutiao-nav .more-wrapper .more-btn {
      position: relative;
      display: block;
      height: 35px;
      width: 40px;
      line-height: 36px;
}
[data-dpr="2"] .toutiao-nav .more-wrapper .more-btn {
        height: 70px;
}
[data-dpr="3"] .toutiao-nav .more-wrapper .more-btn {
        height: 105px;
}
[data-dpr="2"] .toutiao-nav .more-wrapper .more-btn {
        width: 80px;
}
[data-dpr="3"] .toutiao-nav .more-wrapper .more-btn {
        width: 120px;
}
[data-dpr="2"] .toutiao-nav .more-wrapper .more-btn {
        line-height: 72px;
}
[data-dpr="3"] .toutiao-nav .more-wrapper .more-btn {
        line-height: 108px;
}
.toutiao-nav .more-wrapper .more-btn .cross {
        position: absolute;
        display: block;
        height: 17px;
        width: 17px;
        left: 50%;
        top: 50%;
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
}
[data-dpr="2"] .toutiao-nav .more-wrapper .more-btn .cross {
          height: 34px;
}
[data-dpr="3"] .toutiao-nav .more-wrapper .more-btn .cross {
          height: 51px;
}
[data-dpr="2"] .toutiao-nav .more-wrapper .more-btn .cross {
          width: 34px;
}
[data-dpr="3"] .toutiao-nav .more-wrapper .more-btn .cross {
          width: 51px;
}
.toutiao-nav .more-wrapper .more-btn .cross:before,
      .toutiao-nav .more-wrapper .more-btn .cross:after {
        content: ' ';
        position: absolute;
        height: 17px;
        width: 4px;
        background-color: #f85959;
        opacity: .8;
}
[data-dpr="2"] .toutiao-nav .more-wrapper .more-btn .cross:before, [data-dpr="2"]
        .toutiao-nav .more-wrapper .more-btn .cross:after {
          height: 34px;
}
[data-dpr="3"] .toutiao-nav .more-wrapper .more-btn .cross:before, [data-dpr="3"]
        .toutiao-nav .more-wrapper .more-btn .cross:after {
          height: 51px;
}
.toutiao-nav .more-wrapper .more-btn .cross:before {
        left: 50%;
        -webkit-transform: rotate(90deg) translate(0px, 2px);
                transform: rotate(90deg) translate(0px, 2px);
}
.toutiao-nav .more-wrapper .more-btn .cross:after {
        left: 50%;
        -webkit-transform: translate(-50%, 0);
                transform: translate(-50%, 0);
}



