
html {
  background: #fff;
  font-family: STHeiti,Microsoft YaHei,Helvetica,Arial,sans-serif;
  -webkit-text-size-adjust: none;
  word-break: break-word;
}
a {
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
}
.clearfix::after {
  content: ' ';
  display: block;
  clear: both;
}
.line3 {
  -webkit-line-clamp: 3;
}
@font-face {
  font-family: 'toutiao-iconfont';
  /* project id 363854 */
  src: url("//at.alicdn.com/t/font_gunoldcqvx61dcxr.eot");
  src: url("//at.alicdn.com/t/font_gunoldcqvx61dcxr.eot?#iefix") format("embedded-opentype"), url("//at.alicdn.com/t/font_gunoldcqvx61dcxr.woff") format("woff"), url("//at.alicdn.com/t/font_gunoldcqvx61dcxr.ttf") format("truetype"), url("//at.alicdn.com/t/font_gunoldcqvx61dcxr.svg#iconfont") format("svg");
}
.icon {
  font-family: 'toutiao-iconfont';
  font-style: normal;
}
.icon-refresh::before {
  content: '\e62b';
}
#toutiao-header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: no-wrap;
      flex-wrap: no-wrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  text-align: center;
  height: 44px;
  background-color: #D43D3D;
}
[data-dpr="2"] #toutiao-header {
    height: 88px;
}
[data-dpr="3"] #toutiao-header {
    height: 132px;
}
#toutiao-header .position_l > .message-box {
    display: block;
    background: url("./images/feed_ic_message_normal@3x.png") no-repeat center;
    background-size: contain;
    height: 25px;
    width: 32px;
    margin-left: 12px;
}
[data-dpr="2"] #toutiao-header .position_l > .message-box {
      height: 50px;
}
[data-dpr="3"] #toutiao-header .position_l > .message-box {
      height: 75px;
}
[data-dpr="2"] #toutiao-header .position_l > .message-box {
      width: 64px;
}
[data-dpr="3"] #toutiao-header .position_l > .message-box {
      width: 96px;
}
[data-dpr="2"] #toutiao-header .position_l > .message-box {
      margin-left: 24px;
}
[data-dpr="3"] #toutiao-header .position_l > .message-box {
      margin-left: 36px;
}
#toutiao-header .position_m {
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    font-size: 0;
}
#toutiao-header .position_m > .logo {
      display: inline-block;
      background: url("./images/wap_logo@3x.png") no-repeat center;
      background-size: contain;
      height: 44px;
      width: 83px;
}
[data-dpr="2"] #toutiao-header .position_m > .logo {
        height: 88px;
}
[data-dpr="3"] #toutiao-header .position_m > .logo {
        height: 132px;
}
[data-dpr="2"] #toutiao-header .position_m > .logo {
        width: 166px;
}
[data-dpr="3"] #toutiao-header .position_m > .logo {
        width: 249px;
}
#toutiao-header .position_m > .refresh {
      display: inline-block;
      background: url("./images/titlebar_refresh_small@3x.png") no-repeat center;
      background-size: 15px;
      height: 44px;
      width: 25px;
}
[data-dpr="2"] #toutiao-header .position_m > .refresh {
        background-size: 30px;
}
[data-dpr="3"] #toutiao-header .position_m > .refresh {
        background-size: 45px;
}
[data-dpr="2"] #toutiao-header .position_m > .refresh {
        height: 88px;
}
[data-dpr="3"] #toutiao-header .position_m > .refresh {
        height: 132px;
}
[data-dpr="2"] #toutiao-header .position_m > .refresh {
        width: 50px;
}
[data-dpr="3"] #toutiao-header .position_m > .refresh {
        width: 75px;
}
#toutiao-header .position_m .rotage-animate {
      -webkit-animation: rotage360 1s linear infinite;
              animation: rotage360 1s linear infinite;
}
@-webkit-keyframes rotage360 {
0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes rotage360 {
0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
#toutiao-header .position_r > .search {
    display: block;
    background: url("./images/feed_ic_search_normal@3x.png") no-repeat center;
    background-size: contain;
    height: 24px;
    width: 24px;
    margin-right: 12px;
}
[data-dpr="2"] #toutiao-header .position_r > .search {
      height: 48px;
}
[data-dpr="3"] #toutiao-header .position_r > .search {
      height: 72px;
}
[data-dpr="2"] #toutiao-header .position_r > .search {
      width: 48px;
}
[data-dpr="3"] #toutiao-header .position_r > .search {
      width: 72px;
}
[data-dpr="2"] #toutiao-header .position_r > .search {
      margin-right: 24px;
}
[data-dpr="3"] #toutiao-header .position_r > .search {
      margin-right: 36px;
}



