
html {
  background: #fff;
  font-family: STHeiti,Microsoft YaHei,Helvetica,Arial,sans-serif;
  -webkit-text-size-adjust: none;
  word-break: break-word;
}
a {
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
}
.clearfix::after {
  content: ' ';
  display: block;
  clear: both;
}
.line3 {
  -webkit-line-clamp: 3;
}
@font-face {
  font-family: 'toutiao-iconfont';
  /* project id 363854 */
  src: url("//at.alicdn.com/t/font_gunoldcqvx61dcxr.eot");
  src: url("//at.alicdn.com/t/font_gunoldcqvx61dcxr.eot?#iefix") format("embedded-opentype"), url("//at.alicdn.com/t/font_gunoldcqvx61dcxr.woff") format("woff"), url("//at.alicdn.com/t/font_gunoldcqvx61dcxr.ttf") format("truetype"), url("//at.alicdn.com/t/font_gunoldcqvx61dcxr.svg#iconfont") format("svg");
}
.icon {
  font-family: 'toutiao-iconfont';
  font-style: normal;
}
.icon-refresh::before {
  content: '\e62b';
}
#toutiao-main {
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}
#toutiao-main .loading-tip {
    font-size: 16px;
    line-height: 48px;
    color: #999;
    text-align: center;
}
[data-dpr="2"] #toutiao-main .loading-tip {
      font-size: 32px;
}
[data-dpr="3"] #toutiao-main .loading-tip {
      font-size: 48px;
}
[data-dpr="2"] #toutiao-main .loading-tip {
      line-height: 96px;
}
[data-dpr="3"] #toutiao-main .loading-tip {
      line-height: 144px;
}
.vue-touch-refresh {
  position: absolute;
  height: 0;
  width: 100%;
  background-color: transparent;
}
.vue-touch-refresh .refresh-container {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    display: block;
    background-color: transparent;
    z-index: 998;
}
.vue-touch-refresh .refresh-container .refresh {
      display: block;
      opacity: 0;
      border: 1px solid rgba(77, 77, 77, 0.8);
      border-radius: 50%;
      font-size: 60px;
      font-weight: 400;
      color: green;
      background-color: #fff;
      -webkit-transition: all .5s ease-in-out;
      transition: all .5s ease-in-out;
}



